*/

/* VARIABLES
All your sass variable should be declared here
*/

$brand-blue: #2F596E;
$brand-green: #677C6D;


/* MIXINS
All your sass mixins should be declared here
*/

@mixin for-size($size) {
  @if $size == phone {
    @media (max-width: 599px) { @content; }
  }
  @else if $size == tablet {
    @media (min-width: 600px) { @content; }
  }
  @else if $size == desktop {
    @media (min-width: 1024px) { @content; }
  }
}

/* FUNCTIONS
All your sass functions should be declared here
*/

.dots-menu {
  display: none;

  @include for-size(desktop) {
    display: block;
    .sections {
      .section {
        list-style: none;
        padding-top: 10px;
        padding-bottom: 10px;
        margin-top: 6px;

        .text {
          position: relative;
          top: -2px;
          right: 12px;
          opacity: 0;
          color: $brand-blue;
          -webkit-transition: opacity 0.2s;
          -o-transition: opacity 0.2s;
          transition: opacity 0.2s;
          font-weight: bold;
          font-size: 18px;
        }

        .dot {
          display: inline-block;
          width: 20px;
          height: 20px;
          border-radius: 50%;
          background-color: #fff;
          -webkit-transition: background-color 0.2s;
          -o-transition: background-color 0.2s;
          transition: background-color 0.2s;
        }
      }

      &:hover {
        .section {
          color: $brand-blue;
          cursor: pointer;

          .text {
            opacity: 1;
          }

          .dot {
            background-color: $brand-blue;
          }
        }
      }
    }
  }
}



