$brand-blue: #2F596E;
$brand-green: #677C6D;


/* MIXINS
All your sass mixins should be declared here
*/

@mixin for-size($size) {
  @if $size == phone {
    @media (max-width: 599px) { @content; }
  }
  @else if $size == tablet {
    @media (min-width: 600px) { @content; }
  }
  @else if $size == desktop {
    @media (min-width: 1024px) { @content; }
  }
}

/* FUNCTIONS
All your sass functions should be declared here
*/

.overlay-menu {
  display: block;
  position: fixed;
  right: -200px;
  top: 0;
  width: 200px;
  height: 100vh;
  padding-top: 50px;
  background-color: $brand-blue;
  -webkit-transition: right 0.5s;
  -o-transition: right 0.5s;
  transition: right 0.5s;
  z-index: 99;

  .sections {
    text-align: center;

    .section {
      margin-top: 40px;
      font-size: 18px;
      list-style: none;
      color: #fff;
    }
  }

  &.-active {
    right: 0;
  }
}



