$brand-green: #677C6D;


/* MIXINS
All your sass mixins should be declared here
*/

@mixin for-size($size) {
  @if $size == phone {
    @media (max-width: 599px) { @content; }
  }
  @else if $size == tablet {
    @media (min-width: 600px) { @content; }
  }
  @else if $size == desktop {
    @media (min-width: 1024px) { @content; }
  }
}

/* FUNCTIONS
All your sass functions should be declared here
*/

.toggle-qa {
  .image-title {
    width: 100%;

    &:hover {
      cursor: pointer;
    }
  }
}



